.mdl .sidebar {
	position: absolute;
	background-color: #353535;
	border-left: 1px solid #353535;
	box-shadow: 0 0 50px rgba(0, 0, 0, 0.1);
	top: 0;
	right: 0;
	bottom: 0;
	width: 350px;
	transform: translateX(350px);
	padding: 20px;
	transition: transform 0.3s;
	display: grid;
	z-index: 100;
	grid-template-rows: 0.7em 15rem auto auto 0.1em;
	grid-template-columns: auto 85% auto;
	grid-template-areas:
		". . ."
		". SideTop ."
		". SideBottom ."
		". SideBottom ."
		". . .";
}

.mdl.show .sidebar {
	transform: translateX(0);
}

.small-margin-right {
	margin-right: 5px;
}

a {
	text-decoration: none;
}

p {
	color: whitesmoke;
}

.linkText {
	color: whitesmoke;
	font-size: 23px;
	font-weight: 550;
	display: grid;
	margin-block-start: 0;
	margin-block-end: 0;
}

.linkText.sign {
	margin-top: 1em;
	margin-bottom: 1em;
}

// INTRODUCTION => 라즈베리 서비스란?
.intro:after {
	content: "INTRODUCTION";
	text-align: center;
	width: 100%;
	height: 100%;
	transition: 0.2s ease-in-out;
}

.intro:before {
	content: "라즈베리 서비스란?";
	text-align: center;
	opacity: 0;
	top: 23px;
	left: 0;
	font-weight: 600;
	position: relative;
	color: #cf3535;
	transition: all 1s;
}

.intro:hover::before {
	width: 100%;
	opacity: 1;
	text-align: center;
}

.intro:hover::after {
	opacity: 0;
	text-align: center;
}

